'use client';

import { FormEvent } from 'react';
import Link from 'next/link';
import { ArrowLeft, Save, Loader2, Calendar as CalendarIcon, CreditCard, User } from 'lucide-react';
import { format } from 'date-fns';
import { id as localeId } from 'date-fns/locale';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Switch } from '@/components/ui/switch';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import {
    Select, SelectContent, SelectItem, SelectTrigger, SelectValue,
} from '@/components/ui/select';
import {
    Popover, PopoverContent, PopoverTrigger,
} from '@/components/ui/popover';
import { Calendar } from '@/components/ui/calendar';
import { cn } from '@/lib/utils';
import { useCreateBookingForm } from '@/hooks/useCreateBookingForm';

export default function CreateBookingForm() {
    const {
        isSaving,
        lapangans,
        users,
        paymentMethods,
        statuses,
        isManualBooking,
        setIsManualBooking,
        formData,
        setFormData,
        getJamSelesai,
        getEstimasiHarga,
        handleSubmit,
    } = useCreateBookingForm();

    return (
        <form onSubmit={(e: FormEvent) => { e.preventDefault(); handleSubmit(); }} className="space-y-6">
            <Card>
                <CardHeader className="pb-3 border-b mb-4">
                    <div className="flex justify-between items-center">
                        <CardTitle className="text-base flex items-center gap-2">
                            <User className="w-4 h-4" /> Informasi Penyewa
                        </CardTitle>

                        <div className="flex items-center space-x-2">
                            <Label htmlFor="mode-manual" className="text-sm cursor-pointer">
                                {isManualBooking ? 'Pelanggan Umum (Walk-in)' : 'Member Terdaftar'}
                            </Label>
                            <Switch
                                id="mode-manual"
                                checked={isManualBooking}
                                onCheckedChange={setIsManualBooking}
                            />
                        </div>
                    </div>
                </CardHeader>

                <CardContent className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    {isManualBooking ? (
                        <div className="space-y-2">
                            <Label>Nama Pelanggan <span className="text-red-500">*</span></Label>
                            <Input
                                placeholder="Nama Lengkap Pelanggan"
                                value={formData.nama_pengirim}
                                onChange={(e) => setFormData({ ...formData, nama_pengirim: e.target.value })}
                            />
                        </div>
                    ) : (
                        <div className="space-y-2">
                            <Label>Pilih Member <span className="text-red-500">*</span></Label>
                            <Select onValueChange={(v) => setFormData({ ...formData, user_id: v })}>
                                <SelectTrigger>
                                    <SelectValue placeholder="Cari nama member..." />
                                </SelectTrigger>
                                <SelectContent>
                                    {users.map(u => (
                                        <SelectItem key={u.id} value={u.id.toString()}>{u.name} ({u.nomor_telepon || '-'})</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            <p className="text-xs text-gray-500">
                                Belum terdaftar? <Link href="/admin/users" className="text-blue-600 hover:underline">Tambah User Baru</Link> dulu.
                            </p>
                        </div>
                    )}

                    <div className="space-y-2">
                        <Label>Nama Club / Kegiatan</Label>
                        <Input
                            placeholder="Cth: Latihan Rutin, Turnamen Kecil"
                            value={formData.acara}
                            onChange={e => setFormData({ ...formData, acara: e.target.value })}
                        />
                    </div>
                </CardContent>
            </Card>

            <Card>
                <CardHeader className="pb-3 border-b mb-4">
                    <CardTitle className="text-base flex items-center gap-2">
                        <CalendarIcon className="w-4 h-4" /> Jadwal Main
                    </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label>Pilih Lapangan <span className="text-red-500">*</span></Label>
                            <Select onValueChange={(v) => setFormData({ ...formData, lapangan_id: v })}>
                                <SelectTrigger><SelectValue placeholder="Pilih Lapangan" /></SelectTrigger>
                                <SelectContent>
                                    {lapangans.map(l => (
                                        <SelectItem key={l.id} value={l.id.toString()}>{l.nama_lapangan}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>

                        <div className="space-y-2">
                            <Label>Tanggal Main <span className="text-red-500">*</span></Label>
                            <Popover>
                                <PopoverTrigger asChild>
                                    <Button
                                        variant={"outline"}
                                        className={cn(
                                            "w-full justify-start text-left font-normal",
                                            !formData.tanggal_booking && "text-muted-foreground"
                                        )}
                                    >
                                        <CalendarIcon className="mr-2 h-4 w-4" />
                                        {formData.tanggal_booking ? format(formData.tanggal_booking, "PPP", { locale: localeId }) : <span>Pilih tanggal</span>}
                                    </Button>
                                </PopoverTrigger>
                                <PopoverContent className="w-auto p-3 bg-white rounded-xl shadow-xl border-none" align="start" sideOffset={8}>
                                    <div className="bg-white rounded-xl">
                                        <Calendar
                                            mode="single"
                                            selected={formData.tanggal_booking}
                                            onSelect={(date) => setFormData({ ...formData, tanggal_booking: date })}
                                            initialFocus
                                            className="rounded-md border shadow-sm"
                                        />
                                    </div>
                                </PopoverContent>
                            </Popover>
                        </div>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                        <div className="space-y-2">
                            <Label>Jam Mulai <span className="text-red-500">*</span></Label>

                            <Select value={formData.jam_mulai} onValueChange={(v) => setFormData({ ...formData, jam_mulai: v })}>
                                <SelectTrigger>
                                    <SelectValue placeholder="Pilih jam (per jam)" />
                                </SelectTrigger>
                                <SelectContent className="max-h-56 overflow-auto">
                                    {Array.from({ length: 17 }).map((_, i) => {
                                        const hour = 6 + i; 
                                        const value = `${String(hour).padStart(2, '0')}:00`;
                                        return <SelectItem key={value} value={value}>{value}</SelectItem>;
                                    })}
                                </SelectContent>
                            </Select>

                            <p className="text-xs text-gray-500 mt-1">Pilihan per jam â€” menit otomatis <span className="font-medium">:00</span>.</p>
                        </div>
                        <div className="space-y-2">
                            <Label>Durasi (Jam)</Label>
                            <Input type="number" min="1" value={formData.durasi_jam} onChange={e => setFormData({ ...formData, durasi_jam: e.target.value })} />
                        </div>
                        <div className="space-y-2">
                            <Label>Jam Selesai (Est)</Label>
                            <Input value={getJamSelesai()} disabled className="bg-gray-100" />
                        </div>
                    </div>
                </CardContent>
            </Card>

            <Card>
                <CardHeader className="pb-3 border-b mb-4">
                    <CardTitle className="text-base flex items-center gap-2">
                        <CreditCard className="w-4 h-4" /> Pembayaran
                    </CardTitle>
                </CardHeader>
                <CardContent className="space-y-4">
                    <div className="p-4 bg-gray-50 border rounded-lg flex justify-between items-center">
                        <span className="text-sm font-medium">Total Harga Sewa:</span>
                        <span className="text-xl font-bold text-[#D93F21]">
                            {new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR' }).format(getEstimasiHarga())}
                        </span>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label>Metode Pembayaran <span className="text-red-500">*</span></Label>
                            <Select onValueChange={(v) => setFormData({ ...formData, payment_method_id: v })}>
                                <SelectTrigger><SelectValue placeholder="Pilih Metode" /></SelectTrigger>
                                <SelectContent>
                                    {paymentMethods.map(p => (
                                        <SelectItem key={p.id} value={p.id.toString()}>{p.nama_metode}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                        </div>

                        <div className="space-y-2">
                            <Label>Status Awal</Label>
                            <Select
                                defaultValue="1"
                                onValueChange={(v) => setFormData({ ...formData, status_booking_id: v })}
                            >
                                <SelectTrigger><SelectValue placeholder="Pilih Status" /></SelectTrigger>
                                <SelectContent>
                                    {statuses.map(s => (
                                        <SelectItem key={s.id} value={s.id.toString()}>{s.nama_status}</SelectItem>
                                    ))}
                                </SelectContent>
                            </Select>
                            <p className="text-xs text-gray-500">
                                Pilih "Terkonfirmasi" jika pelanggan sudah bayar lunas di kasir.
                            </p>
                        </div>
                    </div>

                    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div className="space-y-2">
                            <Label>Jumlah DP / Bayar (Rp)</Label>
                            <Input
                                type="number"
                                placeholder="0"
                                value={formData.jumlah_dp}
                                onChange={e => setFormData({ ...formData, jumlah_dp: e.target.value })}
                            />
                        </div>

                        <div className="space-y-2">
                            <Label>Asal Bank (Opsional)</Label>
                            <Input
                                placeholder="Cth: BCA / Cash"
                                value={formData.asal_bank}
                                onChange={e => setFormData({ ...formData, asal_bank: e.target.value })}
                            />
                        </div>
                    </div>

                    {!isManualBooking && (
                        <div className="space-y-2">
                            <Label>Nama Pengirim (Opsional)</Label>
                            <Input
                                placeholder="Jika beda dengan nama member"
                                value={formData.nama_pengirim}
                                onChange={e => setFormData({ ...formData, nama_pengirim: e.target.value })}
                            />
                        </div>
                    )}
                </CardContent>
            </Card>

            <div className="flex justify-end gap-4">
                <Link href="/admin/bookings">
                    <Button variant="outline" type="button">Batal</Button>
                </Link>
                <Button type="submit" disabled={isSaving} className="bg-[#D93F21] hover:bg-[#b9351b] px-8">
                    {isSaving ? <Loader2 className="h-4 w-4 animate-spin mr-2" /> : <Save className="h-4 w-4 mr-2" />}
                    Simpan Booking
                </Button>
            </div>
        </form>
    );
}
